import React from 'react';
import useStyles from './styles';
import { Box , CardMedia, Typography} from '@material-ui/core';
const Carousel = (props) =>{

    const { backgroundImage, title,description, titlecolor} = props.content;

    const classes = useStyles(titlecolor);

    return(
        <Box width="100%" className={classes.card}>
   <CardMedia image={backgroundImage} className={classes.media}/>
   <Typography variant="h1" className={classes.overlay1}>
      {title}
   </Typography>
   <Typography variant="h2" className={classes.overlay2}>
       {description}

       
   </Typography>
        </Box>

    );


}

export default Carousel;